import { Pipe, PipeTransform } from '@angular/core';
import * as moment from 'moment';
import Duration = moment.Duration;
import {utils} from "../../utils/utils";

@Pipe({
  name: 'changeExtension'
})
export class ChangeExtensionPipe implements PipeTransform{
  transform(fileName: string, extension: string) : string {
    let index = fileName ? fileName.lastIndexOf('.') : -1;
    return (index != -1) ? utils.urlBuilder('file:///', fileName.substr(0, index) + extension) : 'No_Thumbnail';
  }
}



