import {Injectable} from "@angular/core";
import {AppLoggerService} from "../../shared/services/infra/app-logger.service";
import {AppConfigService} from "../../shared/services/infra/app-config.service";
import {ipcWrapper} from "../../utils/ipcWrapper";
import * as _ from "lodash";
import {RouteService} from "../shared/services/route.service";
import {VideoDeviceService} from "../../shared/services/capture-device/video-device.service";

@Injectable()
export class AreaSelectionService {

  public sessionName: any;
  public screenFullBounds: any;
  public engineBounds: any;
  public electronBounds: any;

  constructor(private log: AppLoggerService,
              private config: AppConfigService,
              private routeService: RouteService,
              private videoDeviceService: VideoDeviceService)
  {

  }


  public getInitialAvailableResolutions()
  {
    return _.concat(this.getInitialSelectedResolution(), this.getPresetResolutions(this.getInitialSelectedResolution()));
  }

  /**
   *
   * @returns {{width: any, height: any}}
   */
  public getInitialSelectedResolution() : any
  {
    return this.electronBounds && this.electronBounds['width'] && this.electronBounds['height'] ?
      {width: this.electronBounds['width'], height: this.electronBounds['height']} :
      this.getPresetResolutions()[0];
  }

  /**
   * will return the preset resolution index if exists, -1 if not
   * @param resolution {width:... , height:...}
   */
  public getPresetResolutionIndex(resolution)
  {
    return _.findLastIndex(this.getPresetResolutions(), presetRes => {
      return presetRes && resolution && resolution['width'] === presetRes['width'] && resolution['height'] === presetRes['height'];
    });
  }

  public setInitialAreaWinPosition()
  {
    return (this.electronBounds && _.isFinite(this.electronBounds['x']) && _.isFinite(this.electronBounds['y'])) ?
      ipcWrapper.sendIPC('setWindowPosition', _.merge(this.electronBounds, {text: 'custom'})) : Promise.resolve();
  }

  public setStayOnOneScreen(doStay: boolean)
  {
    let bounds = doStay ? this.screenFullBounds : null;
    ipcWrapper.sendIPC('stayOnOneScreen', { screenFullBounds: bounds });
  }

  public goToPreRecording(showRectAgain: boolean)
  {
    if (showRectAgain && !_.isEmpty(this.electronBounds)){
      ipcWrapper.sendIPC('show-selection-square', { bounds: this.electronBounds, sessionName: this.sessionName })
    }
    this.routeService.navigate('pre-recording', { previewToShow: this.sessionName });
  }

  // check if win7 is running with dwm disabled - for css transparent issue
  public isWin7AndDwmDisabled()
  {
    return ipcWrapper.sendIPC('is-win7-and-is-dwm-disabled').catch(err => {return false;});
  }

  public moveWindowByStep(widthOffset, heightOffset)
  {
    ipcWrapper.sendIPC('moveWindowByStep', widthOffset, heightOffset);
  }

  public changeWindowSizeByStep(widthOffset, heightOffset)
  {
    ipcWrapper.sendIPC('changeWindowSizeByStep', widthOffset, heightOffset);
  }

  /**
   * if resolution to remove is not empty, remove it from returned value
   * @param resolutionToRemove
   * @returns {any}
   */
  public getPresetResolutions(resolutionToRemove: any = null)
  {
    return _.filter(this.config.getConfigData('config.captureApp.captureZoneSelectionPresets'), (preset) => {
      return _.isEmpty(resolutionToRemove) || preset['width'] !== resolutionToRemove['width'] || preset['height'] !== resolutionToRemove['height'];
    });
  }

  public onAreaSelectionConfirm()
  {
    return ipcWrapper.sendIPC('getWindowsBounds')
      .then(engineAndElectronBounds => {
        return this.videoDeviceService.setVideoCaptureRegion(this.sessionName, engineAndElectronBounds['engineBounds'], engineAndElectronBounds['electronBounds'])
      })
      .catch((err) => {
        // if failed do nothing
        this.log.error('AreaSelection: Failed to set Video Capture Region');
      })
      .then(() => {
        this.goToPreRecording(false);
      });
  }

  setWindowSize(size)
  {
    ipcWrapper.sendIPC('setWindowSize', size);
  }

}



