import {Component, ViewChild} from '@angular/core';
import {SettingsService} from "./settings.service";
import {ConfirmationService} from "primeng/components/common/confirmationservice";
import {TranslateService} from "ng2-translate";
import {AccessibilityService} from "../../../../shared/services/infra/accessability.service";
import {LogCollectorComponent} from "../../../../shared/components/log-collector/log-collector.component";

@Component({
  selector: 'settings',
  templateUrl: './settings.component.html',
  styleUrls:  ['./../../styles/inner-page.scss', './settings.component.scss'],
  providers: [SettingsService]
})
export class SettingsComponent {

  private settingsObject = null;
  private readOnly = true;
  private userPrivacyEnabled = true;
  private isDisabled = false

  constructor(private settingsService: SettingsService,
              private accessibilityService: AccessibilityService,
              private translate: TranslateService,
              private confirmationService: ConfirmationService)
  {
    this.settingsService.getUserPrivacyStatus()
      .then((userPrivacyStatus) => {
        this.userPrivacyEnabled = (userPrivacyStatus === 1);
      })
  }

  ngOnInit()
  {
    this.settingsObject = this.settingsService.getLocalSettingsObj();
    this.settingsService.allowSettingsChanges()
      .then(allow => { this.readOnly = !allow; });
    this.accessibilityService.setAccessibilityAlert('MANAGEMENT.settings_desc');
  }

  async onCancelClick()
  {
    if(this.isDisabled)
      return
    this.isDisabled = true
    try {
      await this.settingsService.cancel();
    } catch (err) {}
    this.isDisabled = false
  }

  async onSaveClick()
  {
    if(this.isDisabled)
      return
    this.isDisabled = true
    try {
      await this.settingsService.saveSettings(this.settingsService.alignRecordingResolutionProperties(this.settingsObject));
    } catch (err) {}
    this.isDisabled = false
  }

  getSaveButtonTooltip()
  {
    return this.readOnly ? 'SETTINGS.no_permissions_to_save' : 'SETTINGS.save_settings'
  }

  shouldDisplaySettings(settingsProperty)
  {
    return typeof settingsProperty !== 'undefined';
  }

  isUploadingLogs()
  {
    return this.settingsService.isUploadingLogs();
  }

  askBeforeLeave() {
    return this.settingsService.askBeforeLeave();
  }

  ngOnDestroy()
  {
    this.settingsObject = null;
  }

}



