import {Component, HostListener} from "@angular/core";
import {PaletteService} from "./palette.service";
import {AppLoggerService} from "../../../../shared/services/infra/app-logger.service";
import {utils} from "../../../../utils/utils";
import {ipcWrapper} from "../../../../utils/ipcWrapper";


@Component({
  selector: 'palette',
  templateUrl: './palette.component.html',
  styleUrls: ['./palette.component.scss']
})
export class PaletteComponent {
  private rangeChangeTimeout;


  @HostListener('document:keyup', ['$event'])
  onKeyUp(event:KeyboardEvent) {
    if (event.code === "Escape") {
      this.changeTool('cursor');
    }
  }

  constructor(private paletteService: PaletteService,
              private log: AppLoggerService) {

    ipcWrapper.registerForNotification('on-textbox:modified', textObj => {
      this.paletteService.addAnnotationCuePoint(textObj);
    });

    ipcWrapper.registerForNotification('on-selection:added', selectedObject => {
      if (selectedObject) {
        this.paletteService.setColor(selectedObject.stroke || selectedObject.fill, false);
      }
    });

    ipcWrapper.registerForNotification('on-escape', () => {
      this.changeTool('cursor');
    });
  }

  changeTool(mode: string) {
    this.paletteService.changeMode(mode)
      .catch((err) => {
        return this.log.info('Error: fail at PaletteComponent: changeTool ' + mode + ' ' + utils.stringifyMsg(err));
        // if there is and illegal mode, do nothings
      });
  }

  changeColor(color: string) {
    this.paletteService.setColor(color)
      .catch((err) => {
        return this.log.info('Error: fail at PaletteComponent: changeColor ' + color + ' ' + utils.stringifyMsg(err));
        // if there is and illegal color, do nothings
      });
  }

  clearCanvas() {
    this.paletteService.clearCanvas()
  }

  getColor() {
    return this.paletteService.getColor();
  }

  getMode() {
    return this.paletteService.getMode();
  }

  onRangeChange() {
    // fire event after 200ms of untouched the range input
    clearTimeout(this.rangeChangeTimeout);
    setTimeout(() => {
      if (this.paletteService.getMode() === 'textBox') {
        this.paletteService.setFontSize()
          .catch((err)=>{
            //do nothing
            return this.log.info('Error: fail at PaletteComponent: onRangeChange setFontSize of textBox' + utils.stringifyMsg(err));
          })
      }
      else {
        this.paletteService.setWidth()
          .catch((err)=>{
            //do nothing
            return this.log.info('Error: fail at PaletteComponent: onRangeChange setWidth of '+ this.getMode() + ' ' + utils.stringifyMsg(err));
          })
      }
    }, 200);
  }

  onWhiteboardClick() {
    this.paletteService.onWhiteboardClick();
  }

  reDrawSketch() {
    return this.paletteService.reDrawSketchWin();
  }

  ngOnDestroy()
  {
    ipcWrapper.unregisterForNotification('on-textbox:modified');
    ipcWrapper.unregisterForNotification('on-selection:added');
  }
}



