import {Injectable} from "@angular/core";
import {RouteService} from "../../../shared/services/route.service";
import {AppConfigService} from "../../../../shared/services/infra/app-config.service";
import {AppLoggerService} from "../../../../shared/services/infra/app-logger.service";
import {utils} from "../../../../utils/utils";
import {AppNotificationsService} from "../../../../shared/services/infra/app-notifications.service";
import {ipcWrapper} from "../../../../utils/ipcWrapper";
import {VideoDeviceSettingsService} from "../../../../shared/services/capture-device/video-device-settings.service";
import {AudioDeviceSettingsService} from "../../../../shared/services/capture-device/audio-device-settings.service";
import * as _ from "lodash";
import {LogCollectorService} from "../../../../shared/components/log-collector/log-collector.service";
import {AppStateService} from "../../../../shared/services/infra/app-state.service";

@Injectable()
export class SettingsService {

  constructor(
    private log: AppLoggerService,
    private appState: AppStateService,
    private routeService: RouteService,
    private appConfig: AppConfigService,
    private appNotifications: AppNotificationsService,
    private logCollectorService: LogCollectorService,
    private audioSettingsService: AudioDeviceSettingsService,
    private videoSettingsService: VideoDeviceSettingsService){}

  public allowSettingsChanges()
  {
    return ipcWrapper.sendIPC('canAccessLocalSettings');
  }

  public getUserPrivacyStatus()
  {
    return ipcWrapper.sendIPC('getPartnerUserPrivacyStatus');
  }

  public saveSettings(configObj) : Promise<void>
  {
    return this.setLocalSettingsObj(configObj)
      .then(() => {
        let reloadSessionPromises = [];
        _.forEach(_.concat(this.appState.getSessionsNames(true), this.appState.getSessionsNames(false)), sessionName => {
          //only video device sessions
          if(sessionName !== 'audio')
            reloadSessionPromises.push(this.videoSettingsService.reloadDeviceDefaultConfiguration(sessionName));
        });
        return Promise.all(reloadSessionPromises);
      })
      .then(() => {
        //need to do so only after video devices finished reloading
        // (audio will call stop / start preview and it needs to happen only after new device configuration will be set)
        return this.audioSettingsService.reloadAudioDeviceDefaultConfiguration();
      })
      .then(() => {
        this.log.info('Successfully updated local settings and refreshed devices configuration: ' + utils.stringifyMsg(this.getLocalSettingsObj()));
        this.appNotifications.sendNotification('SETTINGS.settings_save_successful', 'info');
      })
      .catch((err) => {
        this.log.error('Failed to update local settings and refresh devices with the following configurations: ' +
        utils.stringifyMsg(configObj) + '. ' + err ? utils.stringifyMsg(err) : '');
        this.appNotifications.sendNotification('MANAGEMENT.Save_Failed', 'error');
      })
      .then(() => {
        return this.goToPreRecording();
      });
  }

  public cancel()
  {
    return this.goToPreRecording();
  }

  public getLocalSettingsObj()
  {
    return _.cloneDeep(this.appConfig.getConfigData('config'));
  }

  public alignRecordingResolutionProperties(configObj)
  {
    return this.alignRecordingResolutionProperty(this.alignRecordingResolutionProperty(configObj, 'video'), 'screen');
  }

  public isUploadingLogs()
  {
    return this.logCollectorService.isUploadingLogs();
  }

  public askBeforeLeave()
  {
    return this.logCollectorService.askBeforeLeave();
  }

  /**
   * @param configObj
   * @param type video / screen
   */
  private alignRecordingResolutionProperty(configObj: any, type: string)
  {
    configObj['captureApp']['limits'][type]['defaultHeight'] = parseInt(configObj['captureApp']['limits'][type]['defaultHeight']);
    if(parseInt(configObj['captureApp']['limits'][type]['maxHeight']) < parseInt(configObj['captureApp']['limits'][type]['defaultHeight']))
      configObj['captureApp']['limits'][type]['maxHeight'] = configObj['captureApp']['limits'][type]['defaultHeight'];
    return configObj;
  }

  private setLocalSettingsObj(configObj)
  {
    return this.appConfig.setConfigData('config', configObj, true, true);
  }

  private goToPreRecording() : Promise<any>
  {
    return this.routeService.navigate('pre-recording');
  }


}



