import {Injectable} from "@angular/core";
import {AppConfigService} from "./app-config.service";
import {Observable} from "rxjs";
import {AppStateService} from "./app-state.service";
import {ipcWrapper} from "../../../utils/ipcWrapper";
import {AppLoggerService} from "./app-logger.service";
import {utils} from "../../../utils/utils";
import {AppNotificationsService} from "./app-notifications.service";

@Injectable()
export class AppUpdateService {

  private inProgress;
  private idleCounter;
  private intervalSub;

  constructor(private appNotification:AppNotificationsService,
              private appConfig:AppConfigService,
              private appState:AppStateService,
              private appLog:AppLoggerService)
  {
    this.inProgress = false;
    this.idleCounter = this.getBaseIdleTime();
  }

  public startUpdateInterval() {
    this.intervalSub = Observable.timer(0, 1000).subscribe(t => {
      if (this.idleCounter <= 0) this.checkForUpdates();
      else this.idleCounter--;
    });
  }

  public restartUpdateInterval() {
    this.idleCounter = this.getBaseIdleTime();
  }

  public stopUpdateInterval() {
    if (this.intervalSub) {
      this.intervalSub.unsubscribe();
      this.intervalSub = null;
    }
  }

  private checkForUpdates() {
    //app is during update process or during recording restart idle interval counter
    if (this.inProgress || this.appState.isAppRecording()) this.restartUpdateInterval();
    else {
      this.appLog.info('Checking if update is available');
      this.inProgress = true;
      ipcWrapper.sendIPC("is-update-available")
        .then((ready) => {
          //call install updates, if installation will succeed the app will be re-launched
          if (ready) {
            this.appLog.info('Update is ready, calling installation process');
            ipcWrapper.sendIPC("install-update")
              .then(() => {
              }) // will never happen since installer will restart the application
              .catch((err) => {
                this.appLog.error('Failed to install app update. ' + utils.stringifyMsg(err));
                this.restartUpdateInterval();
                this.inProgress = false;
              });
          } else {
            this.appLog.info('There is no update available');
            this.restartUpdateInterval();
            this.inProgress = false;
          }
        });
    }
  }

  private getBaseIdleTime() {
    let baseIdleTime = this.appConfig.getConfigData('config.shared.updates.appIdleSec');
    return baseIdleTime ? baseIdleTime : 180;
  }

  public checkIfWasUpdated()
  {
    ipcWrapper.sendIPC('was-updated')
      .then((updateData) => {
        if(updateData['updated'])
          this.appNotification.sendNotificationWithParams("NOTIFICATIONS.info_appWasUpdated", {'app_version': updateData['version']}, 'info');
      });
  }
}



