import {Component, ViewEncapsulation, HostListener} from '@angular/core';
import {AppLoggerService} from "../shared/services/infra/app-logger.service";
import {AppService} from "./app.service";
import {UploadStatusNotificationsService} from "./shared/services/upload-status-notifications.service";
import {RouteService} from "./shared/services/route.service";
import {SystemHealthService} from "../shared/services/infra/system-health.service";
import {utils} from "../utils/utils";
import {AccessibilityService} from "../shared/services/infra/accessability.service";
import {ScaleAdaptationService} from "./shared/services/scale-adaptation.service";
import {AppUpdateService} from "../shared/services/infra/app-update.service";
import {AppConfigService} from "../shared/services/infra/app-config.service";
import {SystemDataService} from "../shared/services/infra/system-data.service";

@Component({
  selector: 'app-root',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './app.component.html',
  styleUrls:  ['./app.component.scss'],
  providers: [AppService]
})
export class AppComponent {

  constructor(
      private log: AppLoggerService,
      private appService: AppService,
      private appUpdateService: AppUpdateService,
      private appConfig:AppConfigService,
      private uploadNotifications: UploadStatusNotificationsService,
      private systemData: SystemDataService,
      private sysHealthService: SystemHealthService,
      private routeService: RouteService,
      private accessibilityService: AccessibilityService,
      private scaleAdaptationService: ScaleAdaptationService)

  {
    this.log.info("in AppComponent::constructor");
    this.appService.initLocale()
      .then(() => {
        this.loadApplication();
        this.sysHealthService.start();
        this.appUpdateService.checkIfWasUpdated();
      });
    this.preventDroppingFilesIntoApp();
    //init sessions and redirects to the relevant application page.
    //start app updates intervals only if enabled
    if(this.appConfig.getConfigData('config.shared.updates.enabled')){
      this.appUpdateService.startUpdateInterval();
    }
  }

  private loadApplication()
  {
    this.appService.initCaptureSessions()
        .catch((err) => {
          this.log.error('Failed to init capture sessions before app load. ' + utils.stringifyMsg(err) );
        }) //do nothing
        .then(() => {
          return this.appService.initRecordingState();
        })
        .then((route) => {
          return Promise.all([route, this.appService.loadApp()]); //need to wait for window to be ready after splash screen
        })
        .then(([route]) => {
          this.routeService.navigate(<string>route);
        });
  }

  /**
   * The Main reason why doing it in a method way is to add outline focussquare
   * on tab focus ONLY and not on click focus
   * @param KeyboardEvent
   */
  @HostListener('document:keyup', ['$event'])
  onKeyUp(event:KeyboardEvent) {
    return this.accessibilityService.markNavigation(event);
  }

  @HostListener('document:click', ['$event'])
  onDocumentClick(event:MouseEvent){
    this.appUpdateService.restartUpdateInterval();
    //imitating space key press for staying on the same element
    return this.accessibilityService.markNavigation(<KeyboardEvent>{code: 'Enter', target: event.target});
  }

  preventDroppingFilesIntoApp() {
    document.addEventListener('dragover', (event) => {
      event.preventDefault();
      if(event.dataTransfer && event.dataTransfer.dropEffect)
        event.dataTransfer.dropEffect = "none";
    });

    document.addEventListener('drop', (event) => {
      event.preventDefault();
      if(event.dataTransfer && event.dataTransfer.dropEffect)
        event.dataTransfer.dropEffect = "none";
    });
  }

}



