import {ModuleWithProviders}  from '@angular/core';
import {Routes, RouterModule } from '@angular/router';
import {PreRecordingComponent} from "./pre-recording/pre-recording.component";
import {ActiveRecordingComponent} from "./active-recording/active-recording.component";
import {ManagementWindowComponent} from "./management/components/management-window/management-window.component";
import {LibraryComponent} from "./management/components/library/library.component";
import {InfoComponent} from "./management/components/info/info.component";
import {EntryComponent} from "./management/components/entry/entry.component";
import {EntryFormsGuard} from "./management/route-guards/entry-form-guard.sevice";
import {AreaSelectionComponent} from "./area-selection/area-selection.component";
import {SettingsComponent} from "./management/components/settings/settings.component";
import {AreaSelectionResolver} from "./area-selection/area-selection-resolver.service";
import {SettingsComponentGuard} from "./management/route-guards/settings-component-guard";

const appRoutes: Routes = [
  {path: '', redirectTo: 'pre-recording', pathMatch: 'full'},
  {path: 'pre-recording', component: PreRecordingComponent, pathMatch: 'full'}, // redirect to home page on load
  {path: 'area-selection', component: AreaSelectionComponent, pathMatch: 'full', resolve: { resolverData: AreaSelectionResolver } }, // redirect to home page on load
  {path: 'active-recording', component: ActiveRecordingComponent, pathMatch: 'full'},
  {path: 'management', component: ManagementWindowComponent, children: [
    { path: '', redirectTo: 'library', pathMatch: 'full'},
    { path: 'library', component: LibraryComponent },
    { path: 'entry', component: EntryComponent, canDeactivate: [EntryFormsGuard]},
    { path: 'info', component: InfoComponent },
    { path: 'settings', component: SettingsComponent, canDeactivate: [SettingsComponentGuard] },
  ]}
];

export const routing: ModuleWithProviders = RouterModule.forRoot(appRoutes);



