import {Component, NgZone, Input} from "@angular/core";
import {DevicePreviewService} from "../../../../shared/services/capture-device/device-preview.service";

@Component({
  selector: 'audio-level',
  templateUrl: './audio-level.component.html',
  styleUrls:  ['audio-level.component.scss']
})
export class AudioLevelComponent{

  // in case of background noises/ low voice, we are still want to see some level of change in the bar (cane)
  private static readonly OFFSET_ADDITION: number = 10;

  @Input() audioClass: string;
  @Input() disabled: boolean;
  @Input() hoverEffect: boolean = false;
  @Input() isSmall: boolean = false;

  audioLevel0 = 0;
  audioLevel1 = 0;

  constructor(
    private zone: NgZone,
    private devicePreviewService: DevicePreviewService)
  {
    this.devicePreviewService.audioLeverSubject$.subscribe(
      audioLevel => {
        this.zone.run(() => {
          this.audioLevel0 = audioLevel['Channel0'];
          this.audioLevel1 = audioLevel['Channel1'];
        });
      }
    );
  }

  getAudioLevel()
  {
    let value =  Math.floor((this.audioLevel0 + this.audioLevel1) > 0 ? (this.audioLevel0 + this.audioLevel1) / 2 : 0);
    return (value + AudioLevelComponent.OFFSET_ADDITION) <= 100 ? (value + AudioLevelComponent.OFFSET_ADDITION) : 100;
  }
}



