import {Component} from "@angular/core";
import {AppStateService} from "../../../../shared/services/infra/app-state.service";
import {RouteService} from "../../../shared/services/route.service";
import {ipcWrapper} from "../../../../utils/ipcWrapper";
import * as _ from "lodash";
import {AccessibilityService} from "../../../../shared/services/infra/accessability.service";

@Component({
  selector: 'pre-recording-button',
  templateUrl: './recording-button.component.html'
})
export class RecordingButtonComponent {

  private shiftR_UUID;
  private sessionExists = true;

  constructor(private appState: AppStateService,
              private routeService: RouteService,
              private accessService: AccessibilityService){}

  ngOnInit() {
    this.shiftR_UUID = ipcWrapper.registerShortcut("CommandOrControl+Shift+R",
      ()=>{
        this.startRecording();
      });
  }

  isAnyActiveRecordableSessionExits() {
    let sessionExists = !_.isEmpty(this.appState.getActiveRecordableSessionsIds());
    if(sessionExists !== this.sessionExists) {
      this.sessionExists = sessionExists;
      this.setAccessibilityAlert();
    }
    return !_.isEmpty(this.appState.getActiveRecordableSessionsIds());
  }

  private setAccessibilityAlert()
  {
    let exists = this.sessionExists ? 'enabled' : 'disable';
    this.accessService.setAccessibilityAlert('CAPTURE_DEVICE.recording_' + exists + '_information', 'long');
  }


  canRecord()
  {
    return this.appState.isAppStable() && _.keys(this.appState.getActiveRecordableSessionsIds()).length > 0;
  }

  disableRecButton() {
    return !this.canRecord();
  }

  startRecording() : void {
    if (this.canRecord())
      this.routeService.navigate('active-recording');
  }

  ngOnDestroy() {
    ipcWrapper.unRegisterShortcut("CommandOrControl+Shift+R", this.shiftR_UUID);
  }
}



