import {Injectable} from "@angular/core";
import {Resolve, RouterStateSnapshot, ActivatedRouteSnapshot} from "@angular/router";
import {Observable} from "rxjs";
import {ipcWrapper} from "../../utils/ipcWrapper";
import {AreaSelectionService} from "./area-selection.service";
import * as _ from "lodash";
import {AppConfigService} from "../../shared/services/infra/app-config.service";

@Injectable()
export class AreaSelectionResolver implements Resolve<any> {

  private WIDTH_FALLBACK = 800;
  private HEIGHT_FALLBACK = 600;

  constructor(private areaSelectionService: AreaSelectionService,
              private appConfig: AppConfigService) {}

  resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Observable<any> | Promise<any> | any {
    let scaleFactor;
    let winBounds;


    return Promise.all([
      // we have to put the win on the correct device before doing Anything - for electron to behave properly in the
      // means of setSize and setPosition
      ipcWrapper.sendIPC('centerWindowOnDevice', { screenBounds: this.areaSelectionService.screenFullBounds}),
      ipcWrapper.sendIPC('is-windows')])
      .then(([screen, isWindows]) => {
        scaleFactor = isWindows ? screen['scaleFactor'] : 1;

        if (_.isEmpty(this.areaSelectionService.engineBounds) || _.isEmpty(this.areaSelectionService.electronBounds)){
          let presets = this.appConfig.getConfigData('config.captureApp.captureZoneSelectionPresets');
          if (presets && presets.length > 0) {
            winBounds = {
              width: Math.floor(presets[0].width / scaleFactor),
              height: Math.floor(presets[0].height / scaleFactor)
            };
          }
          else {
            winBounds = {
              width: Math.floor(this.WIDTH_FALLBACK / scaleFactor),
              height: Math.floor(this.HEIGHT_FALLBACK / scaleFactor)
            };
          }
          winBounds.x = null;
          winBounds.y = null;
        }
        else {
          winBounds = this.areaSelectionService.electronBounds;
        }

        return ipcWrapper.sendIPC("setWindowSize", winBounds)
      })
      .then(() => {
        // real center if need only after we located the win in the correct screen and change it size.
        if (winBounds.x === null || winBounds.y === null) {
          return ipcWrapper.sendIPC('centerWindowOnDevice', { screenBounds: this.areaSelectionService.screenFullBounds});
        }
        else {
          return Promise.resolve({});
        }
      })
      .then(()=>{
        return { screenScaleFactor: scaleFactor }
      })
  }
}



