import {Injectable, NgZone} from "@angular/core";
import {ipcWrapper} from "../../../utils/ipcWrapper";
import {Router} from "@angular/router";
import {AppConsts} from "../../app.consts";
import {DevicePreviewService} from "../../../shared/services/capture-device/device-preview.service";
import {DeviceResourcesService} from "../../../shared/services/capture-device/device-resources.service";
import {AppStateService} from "../../../shared/services/infra/app-state.service";
import {AppConfigService} from "../../../shared/services/infra/app-config.service";
import * as _ from "lodash";
import {AppLoggerService} from "../../../shared/services/infra/app-logger.service";

@Injectable()
export class RouteService {
  public isInTransition = false;

  constructor(private router: Router,
              private zone: NgZone,
              private appState: AppStateService,
              private resourcesService : DeviceResourcesService,
              private devicePreviewService: DevicePreviewService,
              private log: AppLoggerService,
              private appConfig: AppConfigService) {
  }

  private navigateToSomeManagementScreens(absolutePath: string = 'management', params) {
    this.log.info('navigateToSomeManagementScreens');
    const size = { width: AppConsts.MANAGEMENT_WIN_WIDTH, height: AppConsts.MANAGEMENT_WIN_HEIGHT };
    params.changeTheAppPosition = params.changeTheAppPosition === undefined ? true : params.changeTheAppPosition;

    return new Promise((resolve) => {
      let originalWindowBounds = {};
      this.areSessionsReady()
        .then(() => {
          this.resourcesService.freeCaptureResources(true)
            .catch(() => {}) //do nothing
            .then(() => {
              // needed to wait for next cycle that angular will hide the current screen that we are navigating from.
              setTimeout(() => {
                this.devicePreviewService.hideAll()
                  .then(() => {
                    return ipcWrapper.sendIPC("getWindowsBounds");
                  })
                  .then((winBounds)=>{
                    originalWindowBounds = winBounds;
                    return ipcWrapper.sendIPC("setWindowSize", size)
                  })
                  .then(() => {
                    return Promise.all([
                      params.changeTheAppPosition ? ipcWrapper.sendIPC("setWindowPosition", {text:'center', bounds: originalWindowBounds}) : Promise.resolve(true),
                      this.router.navigate([absolutePath])
                    ]);
                  })
                  .then(() => {
                    resolve();
                  })
              }, 1);
            });
        })
    });
  }

  private navigateToAreaSelection() {
    return new Promise((resolve) => {
      // needed to wait for next cycle that angular will hide the current screen that we are navigating from.
      setTimeout(() => {
        this.devicePreviewService.hideAll()
          .then(() => {
            return this.router.navigate(['/area-selection']);
          })
          .then(() => {
            resolve();
          })
      }, 1);
    });
  }

  private navigateToPreRecording(params?: any) {
    this.log.info('navigateToPreRecording');
    let originalWindowBounds = {};
    return this.router.navigate(['/pre-recording', params])
      .then((routeSucceeded) => {
        if (routeSucceeded !== false){
          this.isInTransition = true;
          return ipcWrapper.sendIPC("getWindowsBounds")
            .then((winBounds)=> {
              originalWindowBounds = winBounds;
              return ipcWrapper.sendIPC("setWindowSize", { height: AppConsts.VIDEO_PREVIEW_HEIGHT + AppConsts.DEFAULT_WIN_HEIGHT });
            });
        }
        else {
          return Promise.reject('Route had been cancelled');
        }
      })
      .then(() => {
        return ipcWrapper.sendIPC("setWindowPosition", {text:'center', bounds: originalWindowBounds});
      })
      .then(() => {
        setTimeout(() => {
          return this.areSessionsReady()
            .then(() => {
              return this.resourcesService.getCaptureResources();
            })
            .catch(() => {}) //do nothing
        });
      });
  }

  private navigateToActiveRecording() {
    return ipcWrapper.sendIPC("setWindowSize", { height: AppConsts.ACTIVE_RECORDING_HEIGHT, width: AppConsts.ACTIVE_RECORDING_WIDTH })
      .then(() => {
        return ipcWrapper.sendIPC("setWindowPosition",{text:'bottom-right'})
      })
      .then(() => {
        return this.router.navigate(['/active-recording']);
      });
  }

  private setOnTop(isOnTop) {
    return ipcWrapper.sendIPC("setOnTop", isOnTop);
  }

  private setResizable(isResizable) {
    return ipcWrapper.sendIPC("setResizable", isResizable);
  }

  navigate(fullRoute: string, params: any = {}) : Promise<any> {
    let onNavEndPromise: Promise<any>;
    let isOnTop = false;
    let isResizable = false;

    // match all inside route of management (like: management\entry)
    const halfPathMatch = fullRoute.includes('management') ? 'management' : fullRoute;

    switch (halfPathMatch) {
      case 'management':
        this.isInTransition = true;
        onNavEndPromise = this.navigateToSomeManagementScreens(fullRoute, params);
        break;
      case 'active-recording':
      case '/active-recording':
        this.isInTransition = true;
        onNavEndPromise = this.navigateToActiveRecording();
        isOnTop = true;
        break;
      case 'area-selection':
        this.zone.run(() => this.isInTransition = true);
        onNavEndPromise = this.navigateToAreaSelection();
        isResizable = true;
        isOnTop = true;
        break;
      case 'pre-recording':
      case '/pre-recording':
      case '':
      default:
        onNavEndPromise = this.navigateToPreRecording(params);
    }

    return Promise.all([onNavEndPromise, this.setOnTop(isOnTop), this.setResizable(isResizable)])
      .catch(err => { // do nothings.
      })
      .then(() => {
        this.zone.run(() => this.isInTransition = false);
      });
  }

  getCurrentRoute()
  {
    return this.router.url;
  }

  private areSessionsReady() : any
  {
    return _.keys(this.appState.getActiveSessions()).length > 0 ? this.appState.sessionsAreReadyPromise : Promise.resolve();
  }

  refreshPage()
  {
    let currentUrl = this.router.url;
    if(currentUrl.includes('management')) {
      return this.navigate(currentUrl, { changeTheAppPosition: false });
    } else {
      this.rePaint();
    }
    setTimeout(() => {
      ipcWrapper.sendIPC('repaintBrowserWin');
    }, 1000);
  }

  private rePaint()
  {
    this.zone.run(() => this.isInTransition = true);
    setTimeout(() => {
      this.zone.run(() => this.isInTransition = false)
    }, 500);
  }

  /**
   * when set to true, the app is not visible.
   */
  startTransitioning() {
    this.isInTransition = true;
  }

  endTransitioning() {
    this.isInTransition = false;
  }
}



