import {Component, Renderer2} from '@angular/core';
import {AppConfigService} from "../../../../shared/services/infra/app-config.service";
import {ipcWrapper} from "../../../../utils/ipcWrapper";
import * as _ from "lodash";
import {utils} from "../../../../utils/utils";
import {AppLoggerService} from "../../../../shared/services/infra/app-logger.service";
import {TranslateService} from "ng2-translate";
import {ConfirmationService} from "primeng/components/common/confirmationservice";
import {AccessibilityService} from "../../../../shared/services/infra/accessability.service";

@Component({
  selector: 'info',
  templateUrl: './info.component.html',
  styleUrls:  ['./../../styles/inner-page.scss', './info.component.scss'],
})
export class InfoComponent {

  app_version = '';
  app_user = '';
  site_url = '';

  constructor(
    private confirmationService: ConfirmationService,
    private accessibilityService : AccessibilityService,
    private appConfig: AppConfigService,
    private translate: TranslateService,
    private logger: AppLoggerService,
    private renderer: Renderer2) {}

  ngOnInit()
  {
    let config = this.appConfig.getConfigData("config.shared");
    this.app_version = config.app_version;
    if(config.externalSettings && !_.isEmpty(config.externalSettings)) {
      this.app_user = config.externalSettings.userId;
      this.setSiteUrl(config.externalSettings);
      this.accessibilityService.setAccessibilityAlert('MANAGEMENT.info_desc');
    }
  }

  onLinkClick()
  {
    ipcWrapper.sendIPC('open-external-link', this.site_url);
  }

  onSignOut()
  {
    ipcWrapper.sendIPC('is-uploading')
      .then((isUploading) => {
        let deleteMessage = isUploading ? 'MANAGEMENT.SignOut_msg_with_uploads' : 'MANAGEMENT.SignOut_msg_no_uploads';
        this.translate.get(deleteMessage)
          .subscribe((translatedMessage: string) => {
            this.showSignOutConfirmation(translatedMessage);
          });
      });
  }

  //@see https://kaltura.atlassian.net/wiki/spaces/PROD/pages/656736926/Entry+URL+Setting
  private setSiteUrl(externalSettings)
  {
    //for backward compatibility
    if(externalSettings.hasOwnProperty('entryURL_v2')) {
      this.site_url =  !_.isEmpty(externalSettings.entryURL_v2) ? externalSettings.entryURL_v2 : externalSettings.appHost;
    } else {
      let hostingAppType = externalSettings.hostingAppType ? externalSettings.hostingAppType : '';
      this.site_url =  hostingAppType.toLowerCase() === 'kaf' ?
        utils.urlBuilder(externalSettings.appHost, externalSettings.entryURL) :
        externalSettings.appHost;
    }
  }

  private showSignOutConfirmation(confirmMsg)
  {
    this.confirmationService.confirm({
      message: confirmMsg,
      key: "sign-out-recording-key",
      accept: () => {
        this.signOut();
      },
      reject: () => {
        // do nothing.
      }
    });
  }

  private signOut()
  {
    ipcWrapper.sendIPC('removeExternalSettings')
      .then(() => {
        this.renderer.addClass(document.body, 'sign-out-app-fade');
        setTimeout(() => { this.teminateApp(); }, 2000);
      })
      .catch((err) => {
        this.logger.error('Failed to sign out.' + utils.stringifyMsg(err));
      });
  }

  private teminateApp()
  {
    return ipcWrapper.sendIPC('signOutApp')
      .catch((err) => {
        this.logger.error('Failed to sign out.' + utils.stringifyMsg(err));
      });
  }



}



