import { Injectable } from '@angular/core';
import {AppLoggerService} from "../../../../shared/services/infra/app-logger.service";
import {ipcWrapper} from "../../../../utils/ipcWrapper";
import {utils} from "../../../../utils/utils";
import * as _ from "lodash";

@Injectable()
export class LibraryService {

  private recordingList = [];

  constructor(private log: AppLoggerService) {}


  init() {
    this.fetchRecordingList()
      .then(() => {
        this.registerToChangesInList();
      })
      .catch((err) => {
        //do nothing
      });
  }

  getRecordingList() {
    return this.recordingList;
  }

  clearRecordingList()
  {
    this.recordingList = [];
  }

  ngOnDestroy() {
    ipcWrapper.unregisterForNotification('file-upload-notification');
  }

  public fetchRecordingList(){
    return ipcWrapper.sendIPC('get-recording-list')
      .then(jsonFiles => {
        //filter out deleted uploadStatus and sort by recordingStartTime
        this.recordingList = _.sortBy(_.filter((<Array<any>>jsonFiles),
            (json) => { return json['uploadStatus'] !== 'deleted'}), //filter deleted uploads and jsons from exclude list
            (file) => { return -file.recordingStartTime; }); // sort desc
      })
      .catch((err) => {
        this.log.error('Failed to Get Recording List ' + utils.stringifyMsg(err));
        return Promise.reject(err);
      });
  }

  private registerToChangesInList()
  {
    ipcWrapper.registerForNotification('file-upload-notification',
      (event) => {
        let json = event.file;
        switch (event.eventName){
          case 'added':
            this.log.info('upload notification - received file added');
            this.recordingList.unshift(json);
            break;
          case 'changed':
            this.log.info('upload notification - received file changed');
            this.handleChangedFile(json);
            break;
          case 'removed':
            this.log.info('upload notification - received file removed');
            let indexOfFile = this.getIndexOfFile(json);
            if(indexOfFile > -1) {
              this.recordingList.splice(indexOfFile, 1);
            }
            break;
        }
      });
  }

  private getIndexOfFile(json){
    return _.findIndex(this.recordingList, recording => {
      return recording['recordingStartTime'] === json['recordingStartTime'];
    });
  }

  private handleChangedFile(json)
  {
    let indexOfFile = this.getIndexOfFile(json);
    //update only if index exists and the current update is more recent
    if(indexOfFile > -1 && this.recordingList[indexOfFile]['updateTimestamp'] < json['updateTimestamp']) {
      if(json['uploadStatus'] === 'deleted') {
        this.recordingList.splice(indexOfFile, 1); //remove local json object
      } else {
        if(json['uploadStatus'] === 'uploaded') {
          setTimeout(() => {
            this.updateJsonObject(indexOfFile, json);
          }, 3000);
        } else {
          this.updateJsonObject(indexOfFile, json);
        }
      }
    }
  }

  private updateJsonObject(indexOfFile, json)
  {
    this.recordingList[indexOfFile] = {};   // trick for angular change detection
    this.recordingList[indexOfFile] = json; //update local json object

  }

}



