import {Component, Input, ViewEncapsulation, Renderer2} from "@angular/core";

@Component({
  selector: 'active-recording-confirm',
  encapsulation: ViewEncapsulation.None,
  templateUrl: './active-recording-confirm.component.html',
  styleUrls:  ['./active-recording-confirm.component.scss']
})
export class ActiveRecordingConfirmComponent {

  // to open multiple confirm dialogs we need to supply an id when calling with confirmationService
  @Input() confirm_dialog_key: string;
  @Input() reject_button_text: string;
  @Input() accept_button_text: string;

  constructor(private renderer: Renderer2) {
  }

  ngOnInit() {
    this.renderer.addClass(document.body, 'transparent-overlay');
  }

  ngOnDestroy() {
    this.renderer.removeClass(document.body, 'transparent-overlay');
  }
}



