import { Injectable } from '@angular/core';
import {AppLoggerService} from "../../../shared/services/infra/app-logger.service";
import {utils} from "../../../utils/utils";
import {AppConfigService} from "../../../shared/services/infra/app-config.service";
import {ipcWrapper} from "../../../utils/ipcWrapper";
import {ConfirmationService} from "primeng/components/common/confirmationservice";
import {TranslateService} from "ng2-translate";
import {TruncatePipe} from "../../../shared/pipes/truncate.pipe";
import * as _ from 'lodash';
import {AppNotificationsService} from "../../../shared/services/infra/app-notifications.service";
import {KalturaApiService} from "../../../shared/services/infra/kaltura-api.service";

@Injectable()
export class EntryService {

  public entry;

  public tagsListSubscription$;

  public suggestedTags = [];

  constructor(private log: AppLoggerService,
              private truncate: TruncatePipe,
              private kaltura: KalturaApiService,
              private translate: TranslateService,
              private appConfig: AppConfigService,
              private confirmationService: ConfirmationService,
              private appNotifications: AppNotificationsService)
  {
  }

  public deleteRecordingEntry(conformationKey, recordingEntry, callback = null)
  {
    let deleteMessage = recordingEntry.uploadStatus === 'manual' ? 'MANAGEMENT.Delete_Not_Uploaded_Recording_Message' : 'MANAGEMENT.Delete_Uploaded_Recording_Message';
    this.translate.get(deleteMessage, { RecordingName : this.truncate.transform(_.escape(recordingEntry.summary), 38) })
      .subscribe((deleteMessage: string) => {
        this.confirmationService.confirm({
          message: deleteMessage,
          key: conformationKey,
          accept: () => {
            ipcWrapper.sendIPC('delete-recording', recordingEntry.jsonFilePath)
              .then(() => {
                if(callback) { callback(); }
              })
              .catch((err) => {
                this.log.error('Failed to Delete files of the recording, json: ' + recordingEntry.jsonFilePath + ' ' + utils.stringifyMsg(err));
              });
          },
          reject: () => {
            // do nothing.
          }
        });
      });
  }

  public deleteAllUploadedEntries(recordingList, callback = null)
  {
    if (this.checkForUploadedStatus(recordingList)) {
      this.displayDeleteAllUploadedDialog(recordingList, callback);
    } else {
      this.displayNoUploadedEntriesDialog();
    }
  }

  private displayDeleteAllUploadedDialog(recordingList, callback)
  {
    this.translate.get('MANAGEMENT.Delete_All_Uploaded_Recording_Message')
      .subscribe((deleteMessage: string) => {
        this.confirmationService.confirm({
          message: deleteMessage,
          key: "delete-all-uploaded-recordings-key",
          accept: () => {
            return this.deleteUploadedRecordingsFromList(recordingList, callback);
          },
          reject: () => {
            // do nothing.
          }
        });
      });
  }

  private displayNoUploadedEntriesDialog()
  {
    this.translate.get('MANAGEMENT.Delete_All_Uploaded_Recording_Message_No_Uploaded')
      .subscribe((deleteMessage: string) => {
        this.confirmationService.confirm({
          message: deleteMessage,
          key: "delete-all-uploaded-recordings-no-uploaded-key",
          accept: () => {
            // do nothing
          },
          reject: () => {
            // do nothing.
          }
        });
      });
  }

  private deleteUploadedRecordingsFromList(recordingList, callback)
  {
    let clonedRecList = _.cloneDeep(recordingList);
    let deletePromises = [];
    _.forEach(clonedRecList, recording => {
      if(recording.uploadStatus === 'uploaded') {
        deletePromises.push(ipcWrapper.sendIPC('delete-recording', recording.jsonFilePath));
      }
    });

    return Promise.all(deletePromises)
      .then(() => {
        this.log.info('Removed all uploaded media files from local storage');
        if(callback) { callback(); }
      })
      .catch((err) => {
        this.log.error('Failed to Delete media files from local storage ' + utils.stringifyMsg(err));
      });
  }

  private checkForUploadedStatus(recordingList)
  {
    let exists = false;
    _.forEach(recordingList, recording => {
      if(recording.uploadStatus === 'uploaded') {
        exists = true;
        return false; //beaks lodash forEach loop
      }
    });
    return exists;
  }

  /**
   * @see https://kaltura.atlassian.net/wiki/spaces/PROD/pages/656736926/Entry+URL+Setting
   * @param entryId
   * @returns {string|string}
   */
  public getEntryLinkUrl(entryId){
    let externalSettings = this.appConfig.getConfigData('config.shared.externalSettings');
    //for backward compatibility
    if(externalSettings.hasOwnProperty('entryURL_v2')) {
      return _.isEmpty(externalSettings.entryURL_v2) ?
        utils.urlBuilder(externalSettings.appHost, '/media/', entryId) : externalSettings.entryURL_v2;
    } else {
      return utils.urlBuilder(externalSettings.appHost, externalSettings.entryURL, entryId);
    }
  }

  public openEntryAtWebsite(entryId){
    ipcWrapper.sendIPC('open-external-link', this.getEntryLinkUrl(entryId));
  }

  public saveEntryDetails(newEntryDetails)
  {
    let jsonFilePath = newEntryDetails['jsonFilePath'];
    //clean app content from object
    delete newEntryDetails['jsonFilePath'];
    delete newEntryDetails['updateTimestamp'];
    return this.updateUserTagsInPersistency(newEntryDetails.tags)
      .catch(() => {
        this.log.error('failed to update user tags in persistent data');
      })
      .then(() => {
        return ipcWrapper.sendIPC('updateRecJson', jsonFilePath, newEntryDetails, false)
      })
      .then(path => {
        // if succeeded update the value of the entry
        _.assign(this.entry, newEntryDetails);
        this.appNotifications.sendNotification('MANAGEMENT.Saved', 'info');
      })
      .catch((err) => {
        this.appNotifications.sendNotification('MANAGEMENT.Save_Failed', 'error');
        return Promise.reject(err);
      });
  }

  public updateUploadStatus(jsonFilePath, newState) : Promise<any>
  {
    return ipcWrapper.sendIPC('uploadStatus', {target: jsonFilePath, state: newState});
  }

  private UpdateStatusToUploading(jsonFilePath, isReUpload = false) {
    let newState = isReUpload ? 'reUpload' : 'auto';
    let appToken = this.appConfig.getConfigData('config.shared.externalSettings.appToken'); //appToken can be expired - take current
    let appTokenId = this.appConfig.getConfigData('config.shared.externalSettings.appTokenId');
    return ipcWrapper.sendIPC('updateRecJson', jsonFilePath, {appToken: appToken, appTokenId: appTokenId}, true)
      .then(() => { return this.updateUploadStatus(jsonFilePath, newState);})
  }

  public uploadEntry(conformationKey, jsonFilePath, isReUpload = false): Promise<any>
  {
    return this.getDisclaimer().then(disclaimer => {
      if (!disclaimer || Object.keys(disclaimer).length === 0) // no disclaimer - just upload
        return this.UpdateStatusToUploading(jsonFilePath, isReUpload);

      return new Promise((resolve, reject) => {
        this.confirmationService.confirm({
          message: disclaimer.text,
          key: conformationKey,
          accept: () => {
            return ipcWrapper.sendIPC('updateRecJson', jsonFilePath, {AppHostPostUploadApi: [disclaimer.entryAddedNotificationTemplateUrl]}, true)
              .then(() => { return this.UpdateStatusToUploading(jsonFilePath, isReUpload);})
              .then(() => {resolve();});
          },
          reject: () => {
            return reject("Disclaimer not approved");
          }
        });
      });
    });
  }

  private disclaimerLock = false; // getting disclaimer can run only 1 at the same time.
  private getDisclaimer() : Promise<any> {
    if (this.disclaimerLock) return Promise.reject("Disclaimer is already locked");
    this.disclaimerLock = true;
    return ipcWrapper.sendIPC('getUserDisclaimer')
      .then((disclaimer) => {
        this.disclaimerLock = false;
        return disclaimer;
      })
      .catch((err) => {
        this.log.info('Got error on getDisclaimer: ' + err);
        this.disclaimerLock = false;
        this.appNotifications.sendNotification('NOTIFICATIONS.error_noNetwork', 'error');
        return Promise.reject(err);
    });
  }

  public registerToChanges()
  {
    ipcWrapper.registerForNotification('file-upload-notification',
      (event) => {
        let json = event.file;
        if (json.primaryMediaFile === this.entry.primaryMediaFile){
          switch (event.eventName){
            case 'changed':
              this.entry = json;
              break;
          }
        }
      });
  }

  public unregisterToChanges() {
    ipcWrapper.unregisterForNotification('file-upload-notification');
  }

  filterTagsList(queryString)
  {
    // abort previous request
    if (this.tagsListSubscription$)
    {
      this.tagsListSubscription$.unsubscribe();
      this.tagsListSubscription$ = null;
      this.suggestedTags = null;
    }
    this.log.info('filtering tags: ' + queryString);
    this.tagsListSubscription$ = this.kaltura.getSuggestedTags(queryString).subscribe(
      res => {
        this.tagsListSubscription$ = null;
        this.suggestedTags = res;
      },
      err => {
        this.tagsListSubscription$ = null;
        this.suggestedTags = [];
      });
  }

  private updateUserTagsInPersistency(tagsString)
  {
    return this.kaltura.setSuggestedTags(tagsString.split(','));
  }

}



