import {Component, ViewEncapsulation} from "@angular/core";
import {AppContainerProperties} from "../../../../shared/components/app-container/app-container.component";
import {RouteService} from "../../../shared/services/route.service";
import {ipcWrapper} from "../../../../utils/ipcWrapper";

@Component({
  selector: 'management-window',
  templateUrl: './management-window.component.html',
  styleUrls:  ['management-window.component.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ManagementWindowComponent {

  private containerProperties: AppContainerProperties;

  constructor(private routeService: RouteService){
    this.containerProperties = this.getContainerProperties();
  }

  goToPreRecording() {
    this.routeService.navigate('pre-recording');
  }

  getContainerProperties() : AppContainerProperties{
    let properties: AppContainerProperties = {
      header: {
        logoIcon: 'logo-pc-full',
        buttons: ['minimize_to_tray', 'minimize', 'debug'],
        extendedButtons: [{
          title: 'MANAGEMENT.new_recording',
          ariaLabel: 'MANAGEMENT.new_recording_desc',
          class: "rounded-red-btn",
          onClick: () => { this.goToPreRecording(); }
        }]
      },
      navbar: {
        links: [
          {
            linkClass: 'library',
            label: 'MENU.library',
            routerLink: '/management/library'
          },
          {
            linkClass: 'settings-nav',
            label: 'SETTINGS.panel_title',
            routerLink: '/management/settings',
            shouldDisableWhenNotStable: true
          },
          {
            linkClass: 'info-nav',
            label: 'APP_INFO.title',
            routerLink: '/management/info',
            shouldDisableWhenNotStable: true
          }]
      }
    };

    return properties;
  }
}



