import {Injectable} from "@angular/core";
import * as _ from "lodash";
import {VideoDeviceService} from "../../shared/services/capture-device/video-device.service";
import {AppStateService} from "../../shared/services/infra/app-state.service";
import {ipcWrapper} from "../../utils/ipcWrapper";

@Injectable()
export class PreRecordingService {

  constructor(
    private appState: AppStateService,
    private videoDeviceService: VideoDeviceService
  ){}

  isAppNotStable()
  {
    return !this.appState.isAppStable();
  }

  //just minimizing application to try (and not to dock)
  closeApp()
  {
    return ipcWrapper.sendSystemCommand('closeApp');
  }

  isDevicePreviewing(sessionName) {
      return this.videoDeviceService.isDevicePreviewing(sessionName)
  }
}



