import '../polyfills.ts';

import { platformBrowserDynamic } from '@angular/platform-browser-dynamic';
import { enableProdMode } from '@angular/core';
import { environment } from '../environments/environment';
import {AppConfigService} from '../shared/services/infra/app-config.service'
import { AppModule } from './app.module';
import {ipcWrapper} from "../utils/ipcWrapper";

if (environment.production) {
  enableProdMode();
}

ipcWrapper.sendIPC('is-win7')
  .then( (isWin7) => {

    if(isWin7) {
      require("style-loader!./../assets/styles/css_limitations.css");
    }

    AppConfigService.loadConfigData()
      .then(() => {
        return platformBrowserDynamic().bootstrapModule(AppModule);
      })
      .catch((err) => {
        console.log('bootstrapModule error', err);
      });

  });



