import {Injectable} from "@angular/core";
import {ipcWrapper} from "../../../utils/ipcWrapper";
import {AppStateService} from "../../../shared/services/infra/app-state.service";
import {AppLoggerService} from "../../../shared/services/infra/app-logger.service";
import {AppNotificationsService} from "../../../shared/services/infra/app-notifications.service";

@Injectable()
export class UploadStatusNotificationsService {

  notificationsQueue =[];

  constructor(
    private log: AppLoggerService,
    private appState: AppStateService,
    private appNotification: AppNotificationsService
  )
  {
    this.init();
  }

  init()
  {
    this.registerUploadStatusEvents();
    this.registerRecordingPhaseChanges();
  }

  private registerUploadStatusEvents()
  {
    //if app is not recording - display upload notification
    //else queue this notification and display when possible
    ipcWrapper.registerForNotification('upload-status-notification', (notification) => {
      if(this.appState.recordingPhase === 'recording') {
        this.notificationsQueue.push(notification);
      } else {
        this.appNotification.sendNotification(notification.message, notification.severity, notification.url);
      }
    });
  }

  private registerRecordingPhaseChanges()
  {
    this.appState.recordingPhaseUpdate$.subscribe((recPhase) => {
      if(recPhase !== 'recording') {
        this.displayQueuedNotifications();
      }
    });
  }

  private displayQueuedNotifications()
  {
    while (this.notificationsQueue.length && this.appState.recordingPhase !== 'recording') {
      let notification = this.notificationsQueue.shift();
      this.appNotification.sendNotification(notification.message, notification.severity, notification.url);
    }
  }

}



