import {Injectable} from "@angular/core";
import {RouteService} from "../../../personal/shared/services/route.service";
import {ipcWrapper} from "../../../utils/ipcWrapper";
import {AppLoggerService} from "../../../shared/services/infra/app-logger.service";
import {utils} from "../../../utils/utils";


@Injectable()
export class ScaleAdaptationService {

  private resizeTimeout = null;
  private onTheMove = false;
  private fromScreen = null;

  constructor(private routeService: RouteService,
              private logger: AppLoggerService) {

    // If scale is changed, reload component.
    ipcWrapper.registerForNotification('on-display-metrics-changed',
      (scale) => {
        this.routeService.refreshPage();
      });

    this.registerToWindowMove();
  }


  /**
   * When win enter to another screen -> Refresh the current component to handle different scale and size...
   */
  private registerToWindowMove()
  {
    ipcWrapper.registerForNotification('on-window-move', () => {
      clearTimeout(this.resizeTimeout);
      if(!this.onTheMove) {
        this.onTheMove = true;

        ipcWrapper.sendIPC('getCurrentScreen')
          .then(currentScreen => {
            this.fromScreen = currentScreen;
          });
      }
      else { // on the move
        this.resizeTimeout = setTimeout(() => {
          this.onTheMove = false;

          ipcWrapper.sendIPC('getCurrentScreen')
            .then(currentScreen => {
              if (currentScreen && this.fromScreen && currentScreen['id'] !== this.fromScreen.id) {
                this.logger.info('ScaleAdaptationService: registerToWindowMove: App was moved to different Screen - Refreshing component');
                this.routeService.refreshPage();
              }
            })
            .catch(err=> {
              this.logger.error('ScaleAdaptationService: registerToWindowMove: Failed refreshPage' +  utils.stringifyMsg(err));
            })
            .then(()=>{
              this.routeService.endTransitioning();
            })
        }, 400);
      }
    });
  }
}



